@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background: linear-gradient(to right, #2f99c3, #0086b5) !important;

  background: url(/rainbowhaven-react-frontend/static/media/bigBalloon.c49e2575.jpg);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  /* display: flex;
  justify-content: center;
  flex-direction: column; */
  text-align: left;
  margin-top: 10vh;
}

.auth-wrapper-homes {
  display: flex;
  justify-content: center;
}

.auth-inner {
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 2rem;
  border-radius: 15px;
  transition: all 0.3s;
  width: 50%;
}

.auth-wrapper .form-control:focus {
  border-color: #2f5483;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

/* On screens that are 992px or less*/
@media screen and (max-width: 992px) {
}
.auth-inner {
  width: 50%;
}

/* On screens that are 600px or less*/
@media screen and (max-width: 600px) {
  .auth-inner {
    width: 75%;
  }
}

@media screen and (max-width: 300px) {
  .auth-inner {
    width: 100%;
  }
}

