.btn-login {
  color: #ffffff !important;
  background-color: #533c8e;
  border-color: #533c8e;
}

.btn-login:hover,
.btn-login:focus,
.btn-login:active,
.btn-login.active,
.open .dropdown-toggle.btn-login {
  color: #ffffff;
  background-color: #533c8e;
  border-color: #533c8e;
}

.btn-login:active,
.btn-login.active,
.open .dropdown-toggle.btn-login {
  background-image: none;
}

.btn-login.disabled,
.btn-login[disabled],
fieldset[disabled] .btn-login,
.btn-login.disabled:hover,
.btn-login[disabled]:hover,
fieldset[disabled] .btn-login:hover,
.btn-login.disabled:focus,
.btn-login[disabled]:focus,
fieldset[disabled] .btn-login:focus,
.btn-login.disabled:active,
.btn-login[disabled]:active,
fieldset[disabled] .btn-login:active,
.btn-login.disabled.active,
.btn-login[disabled].active,
fieldset[disabled] .btn-login.active {
  background-color: #533c8e;
  border-color: #533c8e;
}

.btn-login .badge {
  color: #533c8e;
  background-color: #ffffff;
}

a:link,
a:visited {
  color: #503ab7;
}

.registration-form {
  opacity: 0.9;
}

body {
  color: #553e65;
  font-weight: bolder;
  background-color: #edf0f2;
  margin-left: auto;
  margin-right: auto;
}
.volonteer_home {
  margin-top: -740px;
  width: 100%;
}

.Item__border {
  width: 100%;
  margin: 1vw 0;
  border-bottom: 10px solid;
  border-image-source: linear-gradient(45deg, #503ab7, #553e65);
  border-image-slice: 1;
}

/* Navbar */
.nav-link {
  color: #503ab7;
}

/* TaskItem */
.Item__card {
  background: #edf9f2;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}

.Item__card-title {
  color: #3c565c;
}

.Item__button {
  background: #533c8e;
  box-shadow: 2px 1px 2px gray;
}

.complete {
  text-decoration: line-through;
}

.Item__card:hover {
  transform: scale(1.05);
}

.card-body {
  text-align: left;
}

.fa {
  color: #edf0f2;
}

/* Add Item */

.AddItem__card {
  background: #edf9f2;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
}

/* ListVolunteers */
.Volunteer__card {
  background-color: #a0b3c7;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}

/* ListClients */
h4 {
  font-size: 14px;
}
/* On screens that are 992px or less, set the background color to blue */
@media screen and (max-width: 992px) {
  h4 {
    font-size: 16px;
  }
}

/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px) {
  h4 {
    font-size: 18px;
  }
}

